body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#030e19;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.logo{height:32px;background:rgba(255,255,255,0.2);margin:16px}.fw-darkbackground{background-color:#2d2d2d}.ant-layout{background-color:#2d2d2d;color:#d9d9d9}.ant-layout-footer{background-color:#030e19}.ant-drawer-wrapper-body{background-color:#030e19}.ant-progress-inner{vertical-align:top}.ant-modal-header{background-color:#001529;border-bottom:1px solid #717171}.ant-modal-title{color:white}.ant-modal-confirm-title{color:white}.ant-modal-close{color:white}.ant-modal-body{color:white;background-color:#2d2d2d}.ant-modal-footer{background-color:#2d2d2d;border-top:1px solid #717171}.ant-modal-confirm-body>.ant-modal-confirm-title{color:white}.ant-modal-confirm-body>.ant-modal-confirm-content{color:white}.overlay{height:100%;width:100%;position:fixed;left:0;top:0;overflow-x:hidden}.overlay-reader{background-color:#000;background-color:rgba(0,0,0,0.9);-webkit-transition:0.5s;transition:0.5s}.overlay-content{position:relative;top:0%;width:100%;text-align:center}.overlay-button{position:relative;box-sizing:border-box}.hamburger-container{float:right;padding:5px 13px 5px 13px;color:#1890ff}.with-shadow{text-shadow:-1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000}

